@charset "utf-8";
.list-scr-navi.scr-navi {
  border-bottom: 0;
}
.scr-navi {
  width: 90.4vw;
  margin: 0 auto;
  .swiper-wrapper {
    .swiper-slide {
      padding: 0 2.666vw 1.733vw;
      a {
        font-size: 4.14vw;
        color: #aaa;
        &.depth-1 {
          color: #1f1f1f;
        }
      }
      .on {
        color: #1f1f1f;
      }
      &:first-child {
        padding-left: 0;
      }
    }
  }
  .icn-navi {
    height: 10.333vw;
    background: url('/_ui/img/m/icn/icn-arrow.png') no-repeat center;
    background-size: 100%;
  }
  a {
    height: 10.333vw;
    line-height: 10.333vw;
  }
  //.swiper-wrapper + .icn-navi {
  //  background: url('/_ui/img/m/icn/scr-navi-bg.png') repeat-y;
  //  background-size: 100%;
  //  width: 4.666vw;
  //  height: 15.333vw;
  //  right: -4.8vw;
  //}
  //.swiper-wrapper + .icn-navi.left {
  //  left: -4.8vw;
  //}
}
.scr-navi .swiper-slide {
  a {
    font-size: 4.14vw;
    color: #aaa;
    &.depth-1 {
      color: #1f1f1f;
    }
  }
  .on {
    color: #1f1f1f;
  }
}
.brand-list-bnr {
  h3 {
    font-size: 3.2vw;
    color: #1f1f1f;
    font-weight: 700;
    text-align: center;
    font-family: 'helvetica';
    padding: 11.466vw 0 5.066vw;
  }
  .bg-image {
    background-position: 50% 50%;
    background-size: cover;
  }
  .brand-list {
    height: 44vw;
    font-size: 0;
  }
  .swiper-slide {
    width: 30.133vw;
  }
}
.brand-list-bnr + .prod-list-top {
  margin: 15.466vw 0 9.333vw;
}
.prod-big-bnr {
  .prod-list-top {
    margin-top: 14.533vw;
  }
}
.bnr-more {
  display: block;
  width: 7.733vw;
  height: 7.733vw;
  background: url('/_ui/img/m/icn/icn-more.png') no-repeat;
  background-size: 100%;
  font-size: 0;
  text-indent: -9999px;
  margin: 15.6vw auto 16.4vw;
}
.list-bnr-more.bnr-more {
  margin: 9.333vw auto 12vw;
}
.prod-list-top.deals {
  margin: 11.333vw 0 6vw;
}
.prod-list-top.mt2 {
  margin: 15.333vw 0 6.666vw;
}
.h1-top {
  position: relative;
  .pop-close {
    background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
    background-size: 100%;
    top: 5.333vw;
    right: 4.8vw;
    width: 4vw;
    height: 4vw;
    font-size: 0;
    text-indent: -9999px;
    display: block;
    position: absolute;
  }
  .pop-close.close2 {
    top: 6.4vw;
  }
  h1 {
    font-size: 4.533vw;
    color: #1f1f1f;
    text-align: center;
    font-weight: 700;
    padding: 5.333vw 0 0;
  }
}
.list2-prod .bnr-more {
  margin: 16.666vw auto 8.666vw;
}
@media (min-width: 750px) {
  .list-scr-navi.scr-navi {
    border-bottom: 0;
  }
  .scr-navi {
    width: 678px;
    .swiper-wrapper {
      .swiper-slide {
        padding: 0 20px 13px;
        a {
          font-size: 31px;
        }
      }
    }
    .swiper-wrapper + .icn-navi {
      width: 35px;
      height: 115px;
      right: -36px;
    }
    .swiper-wrapper + .icn-navi.left {
      left: -36px;
    }
    .icn-navi {
      height: 85px;
    }
    a {
      height: 85px;
      line-height: 85px;
    }
  }
  .scr-navi .swiper-slide {
    a {
      font-size: 31px;
    }
  }
  .brand-list-bnr {
    h3 {
      font-size: 24px;
      padding: 86px 0 38px;
    }
    .brand-list {
      height: 330px;
    }
    .swiper-slide {
      width: 226px;
    }
  }
  .brand-list-bnr + .prod-list-top {
    margin: 116px 0 70px;
  }
  .prod-big-bnr {
    .prod-list-top {
      margin-top: 109px;
    }
  }
  .bnr-more {
    width: 58px;
    height: 58px;
    margin: 117px auto 123px;
  }
  .list-bnr-more.bnr-more {
    margin: 70px auto 90px;
  }
  .prod-list-top.deals {
    margin: 85px 0 45px;
  }
  .prod-list-top.mt2 {
    margin: 115px 0 50px;
  }
  .h1-top {
    .pop-close {
      top: 40px;
      right: 36px;
      width: 30px;
      height: 30px;
    }
    .pop-close.close2 {
      top: 48px;
    }
    h1 {
      font-size: 34px;
      padding: 40px 0 0;
    }
  }
  .list2-prod .bnr-more {
    margin: 125px auto 65px;
  }
}
